.navbar-brand {
    color: #f9b200;
}

.navbar {
    background-color: #333;
    color: #f9b200;
    border: 0px;
}

.navbar-right {
    background-color: #f9b200;
    color: #333;
}

.navbar-right span {
    color: #333;
}

body {
    background-color: #7F7F7F;
    color: #ddd;
}

.panel, .panel-heading {
    color: #fff !important;
    background-color: #333 !important;
    border: 0px;
}

.panel-heading {
    border-bottom: 1px #f9b200 solid;
}

.dropdown-menu {
    background-color: #f9b200;
}

.dropdown.open > a, .dropdown a:hover {
    background-color: #f9b200 !important;
}

.ban-settings {
    border-radius: 10px;
    background-color: #333;
    color: #ddd;
    padding: 20px;
}

.ban-settings h3 {
    margin: 0;
}

.ban-settings hr {
    margin: 20px 10px;
    border-top: 1px solid #f9b200;
}

.unslant {
    transform: skewX(40deg);
}

.deslant {
    transform: skewX(0deg);
}

.slant {
    transform: skewX(-40deg);
}

.form-control {
    background-color: #999;
    color: #ddd;
    border-color: #f9b200;
}

.form-control::-webkit-input-placeholder {
    color: #ddd;
}
.form-control:-ms-input-placeholder {
    color: #ddd;
}
.form-control:-moz-placeholder {
    color: #ddd;
}
.form-control::-moz-placeholder {
    color: #ddd;
}

.col-form-label {
    font-size: 20px;
    color: #eee;
}

.btn-default {
    background-color: #333;
    color: #ddd;
    border: 0;
    font-size: 20px;
}

.btn-default:hover,
.btn-default:active,
.btn-default:active:hover,
.btn-default:active:focus,
.btn-default:focus {
    background-color: #333;
    color: #f9b200;
}

.teamContainer {
    margin-top: 100px;
    color: #ddd;
}

.teamContainer input {
    width: 100%;
}

.banContainer {
    color: #ddd;
    margin-top: 50px;
}

.current-team {
    color: #f9b200;
}

.banItem {
    background-color: #333;
}

.mapItem {
    margin: 5px;
    padding: 20px;
    position: inherit;
}

.mapContainer {
    padding: 5px;
}

.mapContainer h3 {
    margin: 0 0 10px;
}

.banned {
}

.dark-ui {
    background-color: #333;
    border-radius: 10px;
    color: #ddd;
    padding: 10px;
}

.dark-ui > h1 {
    margin: 10px;
}

#message-box {
    background-color: rgb(100, 30, 30);
}

.banned:after {
    content: '\A';
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    background: rgba(0,0,0,0.5);
    opacity: 1;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    border-radius: 10px;
}

.selected {
    color: #f9b200;
}

.manage-group {
    margin-top: 100px;
}

.manage-group,
.manage-group > div {
    background-color: #333;
    border-color: #f9b200;
}

.manage-group input {
    background-color: #999;
    width: 100%;
}

#left-team-container .btn,
#left-team-container .dark-ui,
#right-team-container .btn,
#right-team-container .dark-ui{
    width: 100%;
}

#left-team-container span.ready {
    background-color: blue;
    box-shadow: -5px 5px 5px darkblue;
}

#right-team-container span.ready {
    background-color: red;
    box-shadow: 5px 5px 5px darkred;
}

.list-group-item {
    background-color: #7F7F7F !important;
    border: 1px solid #333 !important;
    color: #ddd !important;
}
.list-group-item:hover {
    background-color: #333 !important;
}

.bright-ui {
    background-color: #f9b200;
    border-radius: 10px;
    color: #333;
    padding: 10px;
}